import { connect } from 'react-redux'
import { engine, dispatch } from '../../engine'
import AppPage from './AppPage'
import processZone from './components/processZone'

const e = engine
const af = e.actionFactory();

function download(file, callback) {
    fetch(file)
    .then(resp => resp.text())
    .then(text => JSON.parse(text))
    .then(json => callback(json, null))
    .catch(ex => callback(null, ex))
}

download("/json/zone.json", (json, error) => {
  if(json) {
    dispatch(af.value("zoneActions", json))
    dispatch(af.value("zoneDistTables", processZone(json)))
  } else {
    console.log(error)
  }
})

const mapStateToProps = (state, live, ownProps) => ({
  state,
  live,
})

const mapRepoToProps = (repo, ownProps) => mapStateToProps(ownProps.engine.head(repo), ownProps.engine.live(repo), ownProps)

const mapDispatchToProps = (dispatch, ownProps) => ({
    updateTabIndex: (index) => dispatch(af.value("selectedTabIndex", index)),
})

const AppContainer = connect(
  mapRepoToProps,
  mapDispatchToProps
)(AppPage)

export default AppContainer
