import React, { Component } from 'react'

import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';

import { List, Map } from "immutable-sorted"

import WarChart from "../components/WarChart"

import GameSetting from "./components/GameSetting"

import GamesDesc from "./GamesDesc"

import '../Table/Table.css'

const SliderWT = Slider.createSliderWithTooltip(Slider)

const paramsDef = {
  "pt": "Playing Threshold",
}
const params = Object.keys(paramsDef)
const paramsMap = Map().withMutations(map => {
  params.forEach(id => map.set(id, List()))
})

export default class WarPage extends Component {

  constructor(props) {
    super(props)
    this.state = {
      list: List(),
      heroMap: paramsMap,
      villainMap: paramsMap,
    }
  }

  componentDidMount() {
    this.props.init()
    this.props.fetchGames()
  }

  async componentDidUpdate(prevProps, prevState, snapshot) {
    const { props } = this
    const wrH = props.warResultH
    const wrV = props.warResultV
    const wrN = props.warResultN

    if(prevProps.warResultH !== wrH || prevProps.warResultV !== wrV || prevProps.warResultN !== wrN) {
      let { list, heroMap, villainMap } = this.state

      params.forEach(id => {
        let hl = heroMap.get(id)
        if(!hl) {
          hl = List()
        }
        hl = hl.set(wrH.getIn(["request", "hero", id]), wrH)
        heroMap = heroMap.set(id, hl)        

        let vl = villainMap.get(id)
        if(!vl) {
          vl = List()
        }
        vl = vl.set(wrV.getIn(["request", "villain", id]), wrV)
        villainMap = villainMap.set(id, vl)        
      })
      this.setState({
        list: list.set(wrN.getIn(["request", "n"]), wrN),
        heroMap,
        villainMap,
      })
    }
  }

  setGame(game) {
    this.setState({list: List(), heroMap: paramsMap, villainMap: paramsMap})
    this.props.setGame(game)
  }

  onSliderPlayersChange(value) {
    const { setPlayers } = this.props
    this.setState({list: List(), heroMap: paramsMap, villainMap: paramsMap})
    setPlayers(value)
  }

  onSliderRanksChange(value) {
    const { setRanks } = this.props
    this.setState({list: List(), heroMap: paramsMap, villainMap: paramsMap})
    setRanks(value)
  }

  onSliderVillain(id, value) {
    const { setVillain } = this.props
    const oldVillainMap = this.state.villainMap
    this.setState({list: List(), heroMap: paramsMap, villainMap: paramsMap.set(id, oldVillainMap.get(id))})
    setVillain(id, value)
  }

  onSliderHero(id, value) {
    const { setHero } = this.props
    const oldHeroMap = this.state.heroMap
    this.setState({list: List(), heroMap: paramsMap.set(id, oldHeroMap.get(id)), villainMap: paramsMap})
    setHero(id, value)
  }

  onSliderNChange(value) {
    const { setN } = this.props
    this.setState({heroMap: paramsMap, villainMap: paramsMap})
    setN(value)
  }

  render() {
    const tdl = {width:"90%", padding: 10, textAlign: "left"}
    const tds = {width:"90%", padding: 10, textAlign: "center"}
    const props = this.props
    const { err, game, players, n, villain, hero, ranks, tanking, tankingMs, warGames } = props

    const playersMarks = {}
    const pa = [...Array(8).keys()]
    pa.forEach(p => playersMarks[p+2] = (p+2).toString())
    const rMarks = {}
    const rr = [...Array(99).keys()]
    rr.forEach(r => rMarks[r+2] = (r+2).toString())
    const ranksMarks = {}
    const ra = [...Array(ranks+1).keys()]
    ra.forEach(r => ranksMarks[r] = (r).toString())
    const nMarks = {}
    const na = [...Array(ranks).keys()]
    na.forEach(r => nMarks[r] = (r).toString())
    
    const gDesc = GamesDesc[game] || {params: {}, title: "", desc: []}
    const gameTitle = gDesc.title
    const gameDesc = gDesc.desc.map((t, i) => <div key={i}>{t}</div>)
    const ids = params.filter(id => gDesc.params[id])
    return <div>
      <h3>Poker War</h3>
      <div>
        <div>{tanking?"Tanking ...":(err || "OK")+" ("+(tankingMs/1000)+"s)"}</div>
        <p/>
        <table style={{width:"90%"}} className="minimalistBlack">
          <thead>
            <tr>
              <th>Parameter</th>
              <th>Value</th>
              <th>Slider</th>
            </tr>
          </thead>
          <tbody>
          <tr>
            <td style={{fontWeight: "bold"}}>Game</td>
            <td className="right"/>
            <td style={tdl}>
              <GameSetting
                games={warGames}
                game={game}
                setGame={this.setGame.bind(this)}
              />
              <p/>
              <p style={{fontWeight: "bold"}}>{gameTitle}</p>
              {gameDesc}
            </td>
          </tr>
          <tr>
            <td style={{fontWeight: "bold"}}>Players</td>
            <td className="right">{players}</td>
            <td style={tds}>
              <SliderWT
                height={50}
                min={2}
                max={9}
                marks={playersMarks}
                value={players}
                step={1}
                dots={true}
                included={false}
                onChange={this.onSliderPlayersChange.bind(this)}
              />
              <br/>
            </td>
          </tr>
          <tr>
            <td style={{fontWeight: "bold"}}>Ranks</td>
            <td className="right">{ranks}</td>
            <td style={tds}>
              <SliderWT
                min={2}
                max={100}
                marks={rMarks}
                value={ranks}
                step={1}
                dots={true}
                included={false}
                onChange={this.onSliderRanksChange.bind(this)}
              />
              <br/>
            </td>
          </tr>
          {ids.map((id, i) => <tr key={i}>
            <td style={{fontWeight: "bold"}}>h{id}</td>
            <td className="right">{hero.get(id)}</td>
            <td style={tds}>
              Hero {paramsDef[id]}
              <br/>
              <SliderWT
                min={0}
                max={ranks-1}
                marks={ranksMarks}
                value={hero.get(id)}
                step={1}
                dots={true}
                included={false}
                onChange={value => this.onSliderHero(id, value)}
              />
              <br/>
            </td>
          </tr>
          )}
          {ids.map((id, i) => <tr key={i}>
            <td style={{fontWeight: "bold"}}>v{id}</td>
            <td className="right">{villain.get(id)}</td>
            <td style={tds}>
              Villain {paramsDef[id]}
              <br/>
              <SliderWT
                min={0}
                max={ranks-1}
                marks={ranksMarks}
                value={villain.get(id)}
                step={1}
                dots={true}
                included={false}
                onChange={value => this.onSliderVillain(id, value)}
              />
              <br/>
            </td>
          </tr>
          )}
          <tr>
            <td style={{fontWeight: "bold"}}>N</td>
            <td className="right">{n}</td>
            <td style={tds}>
              <SliderWT
                min={-1}
                max={ranks-1}
                marks={nMarks}
                value={n}
                step={1}
                dots={true}
                included={false}
                onChange={this.onSliderNChange.bind(this)}
              />
              <br/>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
      <h3>Results</h3>
      <table className="minimalistBlack">
      <tbody>
        <tr>
          <>
          {ids.map((id, i) => <td className="center" key={i}>
            <h3>Hero's Profit by Playing Threshold</h3>
            <h3>(when Villain {id} = {villain.get(id)})</h3>
            {n >= 0 ? <h3>(N = {n})</h3> : null}
            <WarChart list={this.state.heroMap.get(id)} getX={v=>v.getIn(["request", "hero", id])}/>
            {/* <table className="minimalistBlack">
              <thead>
                <tr>
                  <th className="right">Hero {id}</th>
                  <th className="right">Return</th>
                </tr>
              </thead>
              <tbody>
                {this.state.heroMap.get(id).filter(wr => wr).map((wr, i) => {
                  const rn = wr.getIn(["request", "hero", id])
                  let rer = wr.get("er")
                  if(rer !== undefined) {
                    rer = (Math.round(rer * 10000) / 100).toString()+"%"
                  }
                  return <tr style={n === rn ? {backgroundColor: "yellow"} : {}} key={i}>
                    <td className="right">{rn}</td>
                    <td className="right">{rer}</td>
                  </tr>
                }).toArray()}
              </tbody>
            </table> */}
          </td>)}
          {ids.map((id, i) => <td className="center" key={i}>
            <h3>Villain's Profit by Playing Threshold</h3>
            <h3>(when Hero's {id} = {hero.get(id)})</h3>
            {n >= 0 ? <h3>(N = {n})</h3> : null}
            <WarChart neg={true} list={this.state.villainMap.get(id)} getX={v=>v.getIn(["request", "villain", id])}/>
            {/* <table className="minimalistBlack">
              <thead>
                <tr>
                  <th className="right">Villain {id}</th>
                  <th className="right">Return</th>
                </tr>
              </thead>
              <tbody>
                {this.state.villainMap.get(id).filter(wr => wr).map((wr, i) => {
                  const rn = wr.getIn(["request", "villain", id])
                  let rer = wr.get("er")
                  if(rer !== undefined) {
                    rer = (Math.round(rer * 10000) / 100).toString()+"%"
                  }
                  return <tr style={n === rn ? {backgroundColor: "yellow"} : {}} key={i}>
                    <td className="right">{rn}</td>
                    <td className="right">{rer}</td>
                  </tr>
                }).toArray()}
              </tbody>
            </table> */}
          </td>)}
          <td className="center">
            <h3>Hero's Profit by Rank</h3>
            {ids.map((id, i) => <div>
              <h3>(Hero's {id} = {hero.get(id)})</h3>
              <h3>(Villain {id} = {villain.get(id)})</h3>
            </div>)}
            <WarChart list={this.state.list} getX={v=>v.getIn(["request", "n"])}/>
            {/* <table className="minimalistBlack">
              <thead>
                <tr>
                  <th className="right">N</th>
                  <th className="right">Return</th>
                </tr>
              </thead>
              <tbody>
                {this.state.list.filter(wr => wr).map((wr, i) => {
                  const rn = wr.getIn(["request", "n"])
                  let rer = wr.get("er")
                  if(rer !== undefined) {
                    rer = (Math.round(rer * 10000) / 100).toString()+"%"
                  }
                  return <tr style={n === rn ? {backgroundColor: "yellow"} : {}} key={i}>
                    <td className="right">{rn}</td>
                    <td className="right">{rer}</td>
                  </tr>
                }).toArray()}
              </tbody>
            </table> */}
          </td>
          </>
        </tr>
      </tbody>
      </table>
      </div>
  }
}
