
import { PoserWorkersApi } from '../../../api/poserWorkersApi'

import { fromJS } from 'immutable-sorted'

let poserApi

export const ahand = ["A", "K", "Q", "J", "T", "9", "8", "7", "6", "5", "4", "3", "2"]

export default async function getDistTable(settings, chand) {
    const apiType = settings.get("apiType")
    const server = settings.get("apiServer")
    const workers = settings.get("workers")
    const jstSecret = settings.get("jwtSecret")
    const threads = settings.get("threads")
    const calcType = settings.get("calcType")
    const nSims = settings.get("nSims")
    const players = settings.get("players")
    const apiOptions = apiType === "rest" ? {apiType, server, threads} : {apiType}

    if(!poserApi || poserApi.workers !== workers) {
        poserApi = new PoserWorkersApi(workers, jstSecret)
    }

    const dt = new Array(13)
    for(let i = 0; i < 13; i++) {
        const dti = new Array(13)
        dt[i] = dti
        for(let j = 0; j < 13; j++) {
            dti[j] = 0
            const hand = ahand[i]+"h"+ahand[j]+(j>i?"h":"s")+chand.substring(4) // "__________"
            const handEvalResult = await poserApi.HandEval(apiOptions, nSims, players, hand, calcType)
            const { err } = handEvalResult
            if(!err) {
                const er =Math.round(handEvalResult.er * 10000) / 100
                const wp =Math.round(handEvalResult.wp * 10000) / 100
                dti[j] = { hand, er, wp }
            }
        }
    }
    return fromJS(dt)
}