import { connect } from 'react-redux'

import { engine, getState } from '../../engine'

import { PoserApi } from '../../api/poserApi'

import GamesDesc from "./GamesDesc"

import '../../utils/wasm_exec'

const e = engine.subEngine('war')
const af = e.actionFactory();
const he = engine.subEngine('home')
const se = he.subEngine('settings')
const saf = se.actionFactory();

const mapStateToProps = (state, repoHead, ownProps) => ({
  err: state.get("err"),
  game: state.get("game"),
  players: se.get(getState()).get("players"),
  villain: state.get("villain"),
  hero: state.get("hero"),
  n: state.get("n"),
  ranks: state.get("ranks"),
  warGames: state.get("warGames"),
  warResultH: state.get("warResultH"),
  warResultV: state.get("warResultV"),
  warResultN: state.get("warResultN"),
  settings: state.get("settings"),
  tanking: state.get("tanking"),
  tankingMs: state.get("tankingMs"),
})

const mapRepoToProps = (repo, ownProps) => mapStateToProps(e.get(e.head(repo)), e.head(repo), ownProps)

let recalcPending = false
let poserApi

const runWar = async (dispatch) => {
  const state = getState()
  const settings = se.get(state)
  const apiType = settings.get("apiType")
  const server = settings.get("apiServer")
  const workers = settings.get("workers")
  const jstSecret = settings.get("jwtSecret")
  const players = settings.get("players")
  const threads = settings.get("threads")
  const tanking = e.get(state, "tanking")

  const game = e.get(state, "game")
  const nsims = settings.get("nSims")

  const n = e.get(state, "n")
  const ranks = e.get(state, "ranks")

  if(tanking) {
    // Don't run if other calculation is in progress
    recalcPending = true
    return
  }

  if(!poserApi || poserApi.workers !== workers) {
    poserApi = new PoserApi(jstSecret)
  }

  try {
    const startTimeMs = Date.now()
    let err = ""
    dispatch(af.value("tanking", true))
    const apiOptions = apiType === "rest" ? {apiType, server, threads} : {apiType}
    const villainSt = e.get(state, "villain")
    const villain = villainSt.toJS()
    const villainStr = JSON.stringify(villain)
    const heroSt = e.get(state, "hero")
    const hero = heroSt.toJS()
    const heroStr = JSON.stringify(hero)

    const gDesc = GamesDesc[game]
    for(const k of heroSt.keySeq().filter(id => gDesc.params[id])) {
      for(let i = 0; !recalcPending && i < ranks; i++) {
        const hero2 = heroSt.toJS()
        hero2[k] = i
        const heroStr2 = JSON.stringify(hero2)
        const warResp = await poserApi.HandWar(apiOptions, nsims, players, villainStr, heroStr2, n, ranks, game)

        if(warResp.err) {
          err = warResp.err
        } else {
          dispatch(af.value("warResultH", warResp))
        }
      }
    }
    for(const k of villainSt.keySeq().filter(id => gDesc.params[id])) {
      for(let i = 0; !recalcPending && i < ranks; i++) {
        const villain2 = villainSt.toJS()
        villain2[k] = i
        const villainStr2 = JSON.stringify(villain2)
        const warResp = await poserApi.HandWar(apiOptions, nsims, players, villainStr2, heroStr, n, ranks, game)
        if(warResp.err) {
          err = warResp.err
        } else {
          dispatch(af.value("warResultV", warResp))
        }
      }
    }

    for (let i = 0; i < ranks; i++) {
      const warResp = await poserApi.HandWar(apiOptions, nsims, players, villainStr, heroStr, i, ranks, game)
      if(warResp.err) {
        err = warResp.err
      } else {
        dispatch(af.value("warResultN", warResp))
      }
    }

    dispatch(af.value("err", err))
    dispatch(af.value("tanking", false))
    const endTimeMs = Date.now()
    dispatch(af.value("tankingMs", endTimeMs - startTimeMs))

    if(recalcPending) {
      recalcPending = false;
      recalc(dispatch)
    }
  } catch(e) {
    const err = e.toString()
    dispatch(af.value("err", err))
    dispatch(af.value("tanking", false))
    if(recalcPending) {
      recalcPending = false;
      recalc(dispatch)
    }
  }
}

const setGame = (dispatch, game) => {
  dispatch(af.value("game", game))
  runWar(dispatch)
}

// Initialize
const init = (dispatch) => {
  dispatch(af.insert("villain", "pt", 1))
  dispatch(af.insert("hero", "pt", 2))
  runWar(dispatch)
}

const setVillain = (dispatch, id, n) => {
  dispatch(af.insert("villain", id, n))
  runWar(dispatch)
}

const setHero = (dispatch, id, n) => {
  dispatch(af.insert("hero", id, n))
  runWar(dispatch)
}

const setN = (dispatch, n) => {
  dispatch(af.value("n", n))
  runWar(dispatch)
}

const setRanks = (dispatch, ranks) => {
  const state = getState()
  const n = e.get(state, "n")
  if(n >= ranks ) {
    dispatch(af.value("n", ranks-1))
  }
  dispatch(af.value("ranks", ranks))
  runWar(dispatch)
}

const setPlayers = (dispatch, players) => {
  if(players < 2) {
    dispatch(saf.value("players", 2))
  } else if(players > 9) {
    dispatch(saf.value("players", 9))
  } else {
    dispatch(saf.value("players", players))
  }
  runWar(dispatch)
}

const recalc = (dispatch) => {
  runWar(dispatch)
}

const fetchGames = async (dispatch) => {
  const state = getState()
  const settings = se.get(state)
  const apiType = settings.get("apiType")
  const server = settings.get("apiServer")
  const workers = settings.get("workers")
  const jstSecret = settings.get("jwtSecret")
  const threads = settings.get("threads")

  if(!poserApi || poserApi.workers !== workers) {
    poserApi = new PoserApi(jstSecret)
  }

  try {
    const startTimeMs = Date.now()
    let err = ""
    dispatch(af.value("tanking", true))
    const apiOptions = apiType === "rest" ? {apiType, server, threads} : {apiType}
    const warResp = await poserApi.WarGames(apiOptions)
    if(warResp.err) {
      err = warResp.err
    } else {
      dispatch(af.value("warGames", warResp))
    }
    dispatch(af.value("err", err))
    dispatch(af.value("tanking", false))
    const endTimeMs = Date.now()
    dispatch(af.value("tankingMs", endTimeMs - startTimeMs))
  } catch(e) {
    const err = e.toString()
    dispatch(af.value("err", err))
    dispatch(af.value("tanking", false))
  }
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  init: () => init(dispatch),
  setGame: (game) => setGame(dispatch, game),
  setPlayers: (players) => setPlayers(dispatch, players),
  setVillain: (id, n) => setVillain(dispatch, id, n),
  setHero: (id, n) => setHero(dispatch, id, n),
  setN: (n) => setN(dispatch, n),
  setRanks: (ranks) => setRanks(dispatch, ranks),
  runWar: () => runWar(dispatch),
  fetchGames: () => fetchGames(dispatch),
})


const WarContainer = connect(
  mapRepoToProps,
  mapDispatchToProps
)

export default WarContainer
