import React from 'react'
import { LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip, Legend, ReferenceLine } from 'recharts'

import css from './EvalPlayersChart.module.css'

const WarChart = (props) => {
  const { neg } = props
  const data = props.list.filter(v=>v).map(v => {
    const n = props.getX(v)
    const er = v.get("er")
    const wp = v.get("wp")
    const wo = v.get("wo")
    const r = {
      n,
      er: Math.floor(er*10000)/100,
      wp: Math.floor(wp*10000)/100,
      wo: Math.floor((wo>100?100:wo)*100)/100,
    }
    if(neg) {
      r.er = -r.er
      r.wp = -r.wp
      r.wo = -r.wo
    }
    return r
  }
  ).toIndexedSeq().toArray()

  const style = {fontSize: 12}

  return (
  <LineChart style={style} className={css.chart}
    width={320}
    height={250}
    data={data}
    margin={{ top: 5, right: 20, left: 20, bottom: 5 }}
  >
    <CartesianGrid stroke="#c0c0c0" strokeDasharray="3 3"/>
    <XAxis dataKey="n" stroke="black"/>
    <YAxis width={30} stroke="black" unit="%" yAxisId="left" />
    {/* <YAxis width={30} stroke="red" unit=":1" yAxisId="right" orientation="right" /> */}
    <Legend />
    <Tooltip />
    <ReferenceLine yAxisId="left" y={0} stroke="black" strokeDasharray="8 4" />
    <Line name="Return (%)" yAxisId="left" type="monotone" dataKey="er" stroke="black" />
    {/* <Line yAxisId="left" type="monotone" dataKey="wp" stroke="blue" />
    <Line yAxisId="right" type="monotone" dataKey="wo" stroke="red" /> */}
  </LineChart>
  )
}

export default WarChart
