import React, { Component } from 'react'

import Select from 'react-select'

export default class ActionPaths extends Component {

  handleChange = selectedOption => {
    this.props.setActionPath(selectedOption)
  }

  render() {
    const { actionPath, actionPaths } = this.props
    this.options = Object.keys(actionPaths.toJS()).sort().map(ap => ({value: ap, label: ap}))
    const selectedOption = actionPath.toJS()

    return (
      <span style={{width: 150}}>
        <Select 
            value={selectedOption}
            onChange={this.handleChange}
            options={this.options}
        />
      </span>
    )
  }
}
