export const xhand = {
    A: 0,
    K: 1,
    Q: 2,
    J: 3,
    T: 4,
    "9": 5,
    "8": 6,
    "7": 7,
    "6": 8,
    "5": 9,
    "4": 10,
    "3": 11,
    "2": 12,
}

export const ahand = ["A", "K", "Q", "J", "T", "9", "8", "7", "6", "5", "4", "3", "2"]

function processActions(cnt, cards) {
    const a = new Array(13)
    for(let i = 0; i < a.length; i++) {
        const b = new Array(13)
        a[i] = b
        for(let j = 0; j < b.length; j++) {
            const hand = ahand[i > j ? j : i]+ahand[i > j ? i : j]+(j > i ? "s" : "")
            b[j] = {hand, cnt: 0, allCnt: 0, profit: 0, prc: 0, aprc: 0}
        }
    }
    let totalCnt = 0
    Object.keys(cards).forEach(hand => {
        const c1 = hand.substr(0, 1)
        const c2 = hand.substr(1, 1)
        const suited = hand.length > 2 && hand.substr(2, 1) === "s"

        const i = xhand[suited?c1:c2]
        const j = xhand[suited?c2:c1]
        const e = a[i][j]
        const c = cards[hand]
        totalCnt += c.cnt
        e.cnt += c.cnt
        e.allCnt += c.allCnt
        e.profit += c.profit
    })

    for(let i = 0; i < a.length; i++) {
        const b = a[i]
        for(let j = 0; j < b.length; j++) {
            const c = b[j]
            c.prc = (Math.round(10000*c.cnt/totalCnt)/100)
            if(c.allCnt > 0) {
                c.aprc = (Math.round(10000*c.cnt/c.allCnt)/100)
            }
        }
    }

    return a
}

function fillCards(cards) {
    for(let i = 0; i < 13; i++) {
        for(let j = 0; j < 13; j++) {
            const hand = ahand[i > j ? j : i]+ahand[i > j ? i : j]+(j > i ? "s" : "")
            const c = cards[hand]
            if(c) {
                c.hand = hand
                c.allCnt = 0
            } else {
                cards[hand] = {hand, cnt: 0, allCnt: 0, profit: 0}
            }
        }
    }
}

export default function processZone(zone) {

    const map = {}
    Object.keys(zone).forEach(actionsPath => {
        const actions = zone[actionsPath]
        Object.keys(actions).forEach(action => {
            const cards = actions[action].act
            fillCards(cards)
            Object.keys(cards).forEach(hand => {
                const c = cards[hand]
                Object.keys(actions).forEach(aaction => {
                    const ccards = actions[aaction].act
                    const cc = ccards[hand]
                    if(cc) {
                        if(!c.allCnt) {
                            c.allCnt = cc.cnt
                        } else {
                            c.allCnt += cc.cnt
                        }
                    }
                })
            })
        })
        Object.keys(actions).forEach(action => {
            const cnt = actions[action].cnt
            const cards = actions[action].act
            map[actionsPath+"/"+action] = processActions(cnt, cards)
        })
    })
    return map
}
