import React, { Component } from 'react'

import Hand from '../components/Hand'
import EvalResult from '../components/EvalResult'
import EvalCatsTable from '../components/EvalCatsTable'
import EvalPlayersTable from '../components/EvalPlayersTable'
import CombosTable from '../components/CombosTable'
import InputButtons from '../components/InputButtons'
import CardsEntry from '../components/CardsEntry'
import SparseCardsEntry from '../components/SparseCardsEntry'
import DistTable from '../components/DistTable'
import ActionPaths from './components/ActionPaths'
import getDistTable from './components/getDistTable'

import css from './HomePage.module.css'

import '../Table/Table.css'

function getActionPaths(actionPath) {
  const r = []
  for(let i = 2; i < actionPath.length; i++) {
    const a = actionPath.substr(i, 1)
    if(a !== "F") {
      r.push(actionPath.substr(0, i)+"/"+a)
    }
  }
  return r
}

export default class HomePage extends Component {

  constructor(props) {
    super(props)

    this.state = {probTable: null}
  }

  async componentDidMount() {
    const props = this.props
    props.setHand(props.hand)
    const probTable = await getDistTable(props.settings, props.hand)
    this.setState({probTable})
  }

  click(card) {
    const props = this.props
    switch(card) {
      case "Xs":
        props.setCurrentSuit("s")
        break
      case "Xh":
        props.setCurrentSuit("h")
        break
      case "Xd":
        props.setCurrentSuit("d")
        break
      case "Xc":
        props.setCurrentSuit("c")
        break
      case "?":
        break
      default:
        props.addCard(card)
        break
    }
  }

  render() {
    const props = this.props
    const {settings, zone, zoneActions, zoneDistTables, actionPath, setActionPath } = props

    const { probTable } = this.state

    return (
    <div>
    <div className={css.sticky}>
      <Hand {...props}/>
      <hr/>
    </div>
    <h3>
      <InputButtons {...props} />
    </h3>
    <h3>
      <EvalResult {...props}/>
    </h3>
    <h3>
      {settings.get("denseKeyboard") ?
      <CardsEntry
        handArr={props.handArr}
        click={this.click.bind(this)}
        style={{width: "7%"}}
        html={true}
        vertical={true}
        settings={props.settings}/> :
      <SparseCardsEntry
        currentSuit={props.currentSuit}
        handArr={props.handArr}
        click={this.click.bind(this)}
        style={{height: 40, width: 40, margin: 2, fontSize: '20px'}}
        html={true}
        vertical={false}
        settings={props.settings}
      />
      }
    </h3>
    {zone?<h3>
    <div>
    {zoneActions ? <ActionPaths actionPath={actionPath} setActionPath={setActionPath} actionPaths={zoneActions}/> : null}
    </div>
    {getActionPaths(actionPath.get("value")).map((ap, i) => <div key={i}>
      <p>Player {i+1}: {ap}</p>
      <DistTable distTable={zoneDistTables.get(ap)} item="prc"/>
      <DistTable distTable={zoneDistTables.get(ap)} item="cnt"/>
      <DistTable distTable={zoneDistTables.get(ap)} item="profit"/>
      <p>Hands Win Probability</p>
      {probTable ? <DistTable distTable={probTable} item="wp"/> : "Tanking..."}
    </div>)}
      <p>Raise:</p>
      <DistTable distTable={zoneDistTables.get(actionPath.get("value")+"/R")} item="aprc"/>
      <DistTable distTable={zoneDistTables.get(actionPath.get("value")+"/R")} item="prc"/>
      <p>Call:</p>
      <DistTable distTable={zoneDistTables.get(actionPath.get("value")+"/C")} item="aprc"/>
      <DistTable distTable={zoneDistTables.get(actionPath.get("value")+"/C")} item="prc"/>
    </h3>:null}
    <h3>
      <EvalPlayersTable {...props}/>
    </h3>
    <h3>
      <EvalCatsTable {...props}/>
    </h3>
    {props.settings.get("combosTable") && props.handCardsCnt >= 5 ? 
    <h3>
      <CombosTable {...props}/>
    </h3>:null}
    </div>
  )
  }
}
