import React from 'react'

import {CopyToClipboard} from 'react-copy-to-clipboard';

import { ahand } from '../App/components/processZone'

import styles from './DistTable.module.css'
const DistTable = (props) => {
  const { distTable, item } = props
  const thr = item==="aprc" ? [0, 20, 80] : item === "wp" ? [1, 10, 25] : [0.1, 0.5, 1.5]

  function cellValue(i, j) {
    const val0 = distTable.getIn([j, i])
    const val1 = val0 ? val0.get(item) : 0

    const val = Math.round(100*val1)/100
    if(val === 0) {
      return ""
    }
    if(item === "prc" || item === "aprc") {
      return val + "%"
    }

    return val
  }

  function cellStyle(i, j) {
    const style = {}
    if(i === j) {
      style.borderWidth = 3
    }
    const val0 = distTable.getIn([j, i])
    const val = val0 ? val0.get(item) : 0
    if(val === 0) {
      // style.backgroundColor = "lightgray"
    } else if(val <= thr[0]) {
      style.backgroundColor = "white"
    } else if(val <= thr[1]) {
      style.backgroundColor = "#ffff9c"
    } else if(val <= thr[2]) {
      style.backgroundColor = "#ffcf9c"
    } else {
      style.backgroundColor = "#9cff9c"
    }
    return style
  }

  return !distTable ? null : 
    <div>
      <table className={styles.table}>
        <thead>
          <tr className={styles.tr}>
            <th key={-1} title={""} className={styles.th}></th>
            {ahand.map((c, i) => <th key={i} title={c} className={styles.th}>{c}</th>)}
          </tr>
        </thead>
      <tbody>
        {ahand.map((c, j) => <tr key={j} className={styles.tr}>
            <th key={-1} title={c} className={styles.th}>{c}</th>
            {ahand.map((c, i) => <td key={i} title={c} className={styles.td} style={cellStyle(i, j)}>
                {cellValue(i, j)}
            </td>)}
        </tr>)}
      </tbody>
      </table>
      {item === "aprc" ? <><p/>
      <CopyToClipboard text={flopzilla(distTable)}>
        <button>Copy Flopzilla Ranges</button>
      </CopyToClipboard>
      </> : null}
      <p/>
    </div>
}

const flopzilla = (distTable) => {
  let s = ""
  const item = "aprc"

  ahand.forEach((c1, i) => ahand.forEach((c2, j) => {
    const val0 = distTable.getIn([j, i])
    const val1 = val0 ? val0.get(item) : 0

    const p = Math.round(100*val1)/100
    if(p > 0) {
      const cc = (i > j ? c2+c1+"s" : c1+c2+"o")
      if(s.length > 0) {
        s += ","
      }
      if(p === 100) {
        s += cc
      } else {
        s += "["+p+"]"+cc+"[/"+p+"]"
      }
    }
  }))

  return s
}

export default DistTable
